import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

export default new Router({
  routes: [{
    path: '/login',
    name: 'login',
    component: resolve => require(['@/components/Login'], resolve),
    meta: {
      whitePath: true,
      subtitle: '运营管理后台'
    },
  },{
    path: '/systemTip',
    name: 'systemTip',
    component: resolve => require(['@/components/SystemTip'], resolve),
    meta: {
      whitePath: true,
      subtitle: '运营管理后台'
    },
  },{
    path: '/upload',
    name: 'upload',
    component: resolve => require(['@/components/Upload'], resolve),
    meta: {
      whitePath: true,
      subtitle: '运营管理后台'
    },
  },{
    path: '/',
    name: 'index',
    redirect: '/userManage/userInfo',
    component: resolve => require(['@/components/Index'], resolve),
    children: [{
      path: 'userManage',
      name: 'userManage',
      component: resolve => require(['@/components/userManage/Index'], resolve),
      meta: {
        subtitle: '用户信息查询'
      },
      children: [{
        path: 'userInfo',
        name: 'userInfo',
        component: resolve => require(['@/components/userManage/UserInfo'], resolve),
        meta: {
          subtitle: '用户信息查询'
        },
      },{
        path: 'userRelation',
        name: 'userRelation',
        component: resolve => require(['@/components/userManage/UserRelation'], resolve),
        meta: {
          subtitle: '用户关系管理'
        },
      }]
    },{
      path: 'applicationManage',
      name: 'applicationManage',
      component: resolve => require(['@/components/applicationManage/Index'], resolve),
      meta: {
        subtitle: '申请管理'
      },
      children: [{
        path: 'whiteApplication',
        name: 'whiteApplication',
        component: resolve => require(['@/components/applicationManage/WhiteApplication'], resolve),
        meta: {
          subtitle: '申请开通白名单'
        },
      },
      {
        path: 'whiteReview',
        name: 'whiteReview',
        component: resolve => require(['@/components/applicationManage/WhiteReview'], resolve),
        meta: {
          subtitle: '白名单审核'
        },
      }]
    }],
  },{
    path: '*', redirect: '/login'
  }],
});




