import { Message } from 'element-ui';
import axios from 'axios';
import jtCookie from '@/assets/js/cookie';
import router from '@/router';

axios.defaults.timeout = 1000 * 30;

// 请求前带去掉null
axios.interceptors.request.use(
  config => {
    if (config.data) {
      for(var i in config.data){
        if (config.data[i] == null) {
          delete config.data[i]
        }
      }
    }
    if (config.params) {
      for(var i in config.params){
        if (config.params[i] == null) {
          delete config.params[i]
        }
      }
    }
    return config;
  },
  err => {
    Message.error({ message: '请求超时!' });
    return Promise.reject(err);
  }
);

// 请求后处理
axios.interceptors.response.use((data) => {
  if (data.data.code === 1004) {
    Message.warning({ message: '登录失效,请重新登录' });
    jtCookie.clearAll();
    router.push('/login');
  } else if (data.data.code === 0 || data.data.errcode === 0) {
    return data.data;
  } else {
    Message.error({ message: data.data.msg });
    return Promise.reject(data.data.msg);
  }
}, (err) => {
  Message.error({ message: '加载失败' });
  return Promise.reject(err);
});

export default axios;



