
var cookie = {
    /**
     * 设置一个cookie,还有一点需要注意的，在qq.com下是无法获取v.qq.com的cookie，反正v.qq.com下能获取到qq.com的所有cookie.
     * 简单得说，子域可以获取根域下的cookie, 但是根域无法获取子域下的cookie.
     *
     * @param {String}
     *          name cookie名称
     * @param {String}
     *          value cookie值
     * @param {String}
     *          domain 所在域名
     * @param {String}
     *          path 所在路径
     * @param {Number}
     *          hour 存活时间，单位:小时
     * @return {Boolean} 是否成功
     *         @example
     *         Live.cookie.set('value1',Live.dom.get('t1').value,"v.qq.com","/v5",24); //设置cookie
     */
    set : function(name, value, domain, path, hour) {
        if (hour) {
            var expire = new Date();
            expire.setTime(expire.getTime() + 3600000 * hour);
        }
        document.cookie = name + "=" + value + "; " + (hour ? ("expires=" + expire.toGMTString() + "; ") : "") + (path ? ("path=" + path + "; ") : "path=/; ") + (domain ? ("domain=" + domain + ";") : ("domain=" + document.location.hostname + ";"));
        return true;
    },

    /**
     * 获取指定名称的cookie值
     *
     * @param {String}
     *          name cookie名称
     * @return {String} 获取到的cookie值
     *         @example
     *         Live.cookie.get('value1'); //获取cookie
     */
    get : function(name) {
        // ryan
        // var s = ' ' + document.cookie + ';', pos;
        // return (pos = s.indexOf(' ' + name + '=')) > -1 ? s.slice(pos +=
        // name.length + 2, s.indexOf(';', pos)) : '';

        var r = new RegExp("(?:^|;+|\\s+)" + name + "=([^;]*)"),
            m = document.cookie.match(r);
        return (!m ? "" : m[1]);
    },

    /**
     * 删除指定cookie,复写为过期
     *
     * @param {String}
     *          name cookie名称
     * @param {String}
     *          domain 所在域
     * @param {String}
     *          path 所在路径
     *          @example
     *          Live.cookie.del('value1'); //删除cookie
     */
    del : function(name, domain, path) {
        document.cookie = name + "=; expires=Mon, 26 Jul 1997 05:00:00 GMT; " + (path ? ("path=" + path + "; ") : "path=/; ") + (domain ? ("domain=" + domain + ";") : ("domain=" + document.location.hostname + ";"));
    },

    clearAll: function() {
        var keys = document.cookie.match(/[^=;]+(?=)/g);
        if (keys) {
            for (let i = keys.length; i--;) {
                this.del(keys[i]);
            }
        }
    }
};

export default cookie;




