import 'element-ui/lib/theme-chalk/index.css';
import ElementUI from 'element-ui';
import axios from '@/assets/js/axios';
import Vuex from 'vuex';
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import '@/assets/scss/index.scss';

Vue.use(Vuex);
Vue.use(ElementUI, { size: 'medium', zIndex: 3000 });
Vue.config.productionTip = false;
Vue.prototype.$axios = axios;

// 路由，进页面前判断是否要登陆信息
router.beforeEach((to, from, next) => {
  const ua = navigator.userAgent.toLowerCase();
  if ((ua.indexOf('webkit') <= -1) && !store.state.hasDisplaySysTip) {
    store.state.hasDisplaySysTip = true;
    next({
      path: '/systemTip',
    });
  } else if (to.meta.whitePath) {
    next();
  } else {
    store.dispatch('checkLogin', {
      success(data) {
        if (data.permissionData.permList.indexOf(to.path) >=0) {
          next()
        } else {
          next({
            path: data.permissionData.permList[0]
          });
        }
      },
      error() {
        next({
          path: '/login',
          query: {
            url: to.path,
          },
        });
      },
    }).catch((err) => {
      console.log(err);
      next({
        path: '/login',
        query: {
          rurl: to.path,
        },
      });
    });
  }
});


new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>',
});



