import config from './config';

const HOST = config.global[config.env].host;
const AUTH_HOST = config.global[config.env].auth_host;

const api = {
  loginApi: `${AUTH_HOST}/api/user/auth-ticket`,                              // 登陆
  getUserInfoApi: `${AUTH_HOST}/api/user/getuserinfo`,                        // 得到用户信息
  getUserPermissionApi: `${AUTH_HOST}/api/user/get-user-permission`,          // 菜单接口
  whiteApplyListApi: `${HOST}/api/business/whitepages/apply_list`,            // 白名单申请列表
  whiteCreateApi: `${HOST}/api/business/whitepages/create`,                   // 添加白名单
  whiteDetailApi: `${HOST}/api/business/whitepages/apply-detail`,             // 申请开通白名单查看详情
  whiteCancelApi: `${HOST}/api/business/whitepages/revoke`,                   // 白名单撤回
  whiteAuditListApi: `${HOST}/api/business/whitepages/audit_list`,            // 白名单审核列表
  whiteAuditApi: `${HOST}/api/business/whitepages/audit`,                     // 白名单审核
  whiteParamsApi: `${HOST}/api/business/whitepages/params`,                   // 白名单审核查询参数
  userListApi: `${HOST}/api/business/users/query`,                            // 用户列表
  userOperateApi: `${HOST}/api/business/users/operate`,                       // 用户状态操作
  usersExportInfoApi: `${HOST}/api/business/users/export-info`,               // 导出用户信息
  usersExportQrcodeApi: `${HOST}/api/business/users/export-qrcode`,           // 导出二维码
  // userRelationListApi: `${HOST}/api/business/relation/get`,                   // 用户关系列表
  userRelationQueryApi: `${HOST}/api/business/relation/query`,                // 查询用户关系
  relationExportApi: `${HOST}/api/business/relation/export`,                  // 导出用户关系
  relationAddApi: `${HOST}/api/business/relation/add`,                        // 添加邀请人
};

export default api;



