import api from '@/api';
import axios from 'axios';
import jtCookie from '@/assets/js/cookie';
import config from '@/api/config.js'
let clientId = config.global[config.env]['client_id'];
let systemId = config.global[config.env]['system_id'];

function getUserPermission() {
  return axios.post(api.getUserPermissionApi, {ticket: jtCookie.get('ticket'), client_id: clientId, systemId: systemId})
}
function getUserInfo() {
  return axios.post(api.getUserInfoApi, {ticket: jtCookie.get('ticket'), client_id: clientId, systemId: systemId})
}



export default {
  state: {
    loginInfo: {
      hasRequest: false,
      isLogin: false,
      username: '', // 手机号，以前的company_id 和 ms_username
    },
    // 用户信息
    userInfo: {},
    // 权限路由信息
    permissionData: {},
    adminLevel: 0
    
  },
  getters: {
    getInitialInfo(state) {
      return state;
    },
    getLoginInfo(state) {
      return state.loginInfo;
    },
    getUserInfo(state) {
      return state.userInfo
    },
    getPermissionData(state) {
      return state.permissionData;
    },
    getAdminLevel(state) {
      return state.adminLevel;
    }
  },
  mutations: {
    setLoginInfo(state, params) {
      Object.assign(state.loginInfo, params);
    },
    setUserInfo(state, params) {
      Object.assign(state.userInfo, params);
    },
    setPermissionData(state, params) {
      Object.assign(state.permissionData, params);

      // 设置级别 ["/applicationManage/whiteApplication", "/applicationManage/whiteReview", "/userManage/userInfo", "/userManage/userRelation"]
      let adminLevel = 0;
      if (params.permList.indexOf('/applicationManage/whiteApplication') >= 0 &&
          params.permList.indexOf('/applicationManage/whiteReview') >= 0 &&
          params.permList.indexOf('/userManage/userInfo') >= 0 &&
          params.permList.indexOf('/userManage/userRelation') >= 0) {
            adminLevel = 4;
      } else if (params.permList.indexOf('/userManage/userInfo') >= 0 && params.permList.indexOf('/userManage/userRelation') >= 0) {
        adminLevel = 1;
      } else if (params.permList.indexOf('/applicationManage/whiteApplication') >= 0) {
        adminLevel = 2;
      } else if (params.permList.indexOf('/applicationManage/whiteReview') >= 0) {
        adminLevel = 3;
      }
      state.adminLevel = adminLevel;
    },
  },
  actions: {

    checkLogin({ commit, state }, { params = {}, success, error, force = false }) {
      if (!jtCookie.get('ticket')) {
        commit('setLoginInfo', {
          isLogin: false,
          hasRequest: false,
        });
        error && error();
        return;
      }
      if (!force && state.loginInfo.hasRequest) {
        if (state.loginInfo.isLogin) {
          success && success(state);
        } else {
          error && error();
        }
        return;
      }

      axios.all([getUserInfo(), getUserPermission()])
        .then(axios.spread((res1, res2) => {
          commit('setLoginInfo', {
            hasRequest: true,
            isLogin: true,
          });
          commit('setUserInfo', res1.data);
          commit('setPermissionData', res2.data);
          success && success({
            userInfo: res1.data,
            permissionData: res2.data
          });
        })).catch((e) => {
          console.log(e);
          error && error();
        });
    },

    doLogin({ commit, state }, { params = {}, success, error }) {
      return axios.post(api.loginApi, params).then(res => {
        jtCookie.set('ticket', res.data.ticket);
        commit('setLoginInfo', {
          islogin: true,
          username: params.account
        });
        success && success(res.data);
      }).catch(err => {
        error && error(err);
      });
    },

    doLogout({ commit, state }, { params = {}, success, error }) {
      jtCookie.clearAll();
      success && success();
    },

  },
};



